<template>
  <div class="edit-modal flex justify-center align-center">
    <div
      class="edit-modal__content flex-column"
      v-on-clickaway="closeModal"
    >
      <label class="w100 default-label flex align-center">
        <p>
          Название урока:
        </p>
        <input
          type="text"
          placeholder="Введите название"
          :value="editForm.name"
          @input="changeEditForm({
            key: 'name',
            value: $event.target.value
          })"
        >
      </label>

      <ckeditor
        class="w100"
        :value="editForm.text"
        :editor="editor"
        @input="changeEditForm({
          key: 'text',
          value: $event
        })"
      />

      <label class="w100 default-label pt-20">
        <p class="pb-20">
          <span>
            Видео урока
          </span>

          <span v-if="editForm.id">
            (
            <a
              target="blank"
              :href="editForm.video_url"
            >
              текущее видео
            </a>
            )
          </span>
        </p>
        <input
          id="lessonVideo"
          type="file"
          @change="changeEditForm({
            key: 'video_file',
            value: $event.target.files[0]
          })"
        >
      </label>

      <input
        class="default-button"
        type="submit"
        value="Сохранить"
        @click.prevent="saveHandler"
        @keydown.enter="saveHandler"
      >
    </div>
  </div>
</template>

<script>
import {
  mapGetters,
  mapMutations,
  mapActions
} from 'vuex'
import { directive as onClickaway } from 'vue-clickaway'
import ClassicEditor from '@ckeditor/ckeditor5-build-classic'

export default {
  directives: {
    onClickaway: onClickaway
  },
  data: () => ({
    editor: ClassicEditor,
    editorData: '',
    editModalIsActive: false
  }),
  props: {
    isActive: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapGetters('Lessons', {
      editForm: 'editForm'
    })
  },
  methods: {
    ...mapActions('Lessons', {
      createLesson: 'createLesson',
      updateLesson: 'updateLesson'
    }),
    ...mapMutations('Lessons', {
      changeEditForm: 'changeEditForm'
    }),
    saveHandler () {
      let apiMethod

      if (this.editForm.id) {
        apiMethod = 'updateLesson'
      } else {
        apiMethod = 'createLesson'
      }

      this[apiMethod]()
        .then(() => {
          this.closeModal()
        })
    },
    closeModal () {
      if (this.isActive) {
        this.$emit('closeModal')
      }
    }
  }
}
</script>
